.history-page{
    color: aliceblue;
    
    overflow-x: auto;
}
.history-page h2{
    color: rgb(145, 56, 223);
    text-align: center;
    margin: 20px;
    text-transform: uppercase;
    letter-spacing: 1.2px;
}
.history-page h4{
    color: rgb(144, 55, 223);
    text-align: center;
    margin: 20px;
    text-transform: uppercase;
    letter-spacing: 1.2px;
}
.history-page table{
    color: aliceblue;
    margin: auto;
    width: 100%;
    margin-top: 5px;
}
.history-page table,th,tr,td{
    color: aliceblue;
    border: 1px solid #ddd;
    border-collapse: collapse;
}
th, td{
    color: aliceblue;
    text-align: center;
    padding: 10px;
    text-transform: capitalize;
}
table a{
    color: rgb(3, 165, 206);
}

.history-page img{
    width: 70px;
    height: 100px;
    object-fit: cover;
}
.earnings-table h3{
    margin-top: 10px;
    margin-bottom: 8px;
    color: rgb(144, 55, 223);

}