.detail {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 50px;
  font-size: 1.5rem;
}

.detail img {
  max-width: 100%;
  margin: 20px;
  height: 450px;
  object-fit: cover;
  display: block;
}

.box-detail {
  max-width: 500px;
  width: 100%;
  margin: 20px;
}

.box-detail .row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.box-detail h2 {
  text-transform: uppercase;
  color: #D0E6F0;
  letter-spacing: 2px;
  font-weight: bold;
  font-size: 2rem;
  margin-bottom: 10px;
}

.box-detail p {
  line-height: 1.6;
  margin: 10px 0;
  opacity: 0.8;
  color:white;
}
.box-detail span{
 
  color:#FBE5C0;
}
.box-detail .cart {
  background: #333;
  color: white;
  margin-top: 15px;
  padding: 10px 20px;
  display: inline-block;
  text-transform: uppercase;
  letter-spacing: 2px;
  border: none;
  cursor: pointer;
  font-size: 1.2rem;
}

h2 {
  margin-bottom: 20px;
  font-size: 2.5rem;
  color: #D0E6F0;
}
/* ... (previous styles) ... */

.section-heading {
  margin-top: 20px;
}

.section-heading h3 {
  font-size: 1.8rem;
  color: #D0E6F0;
  margin-bottom: 10px;
}

